<template>
    <div id="takeout-timelimit">
        <public-header title="时限要求"></public-header>
        <div class="content">
            <van-cell-group>
                <van-cell class="border-0px">
                    <div slot="title">支付时间限制</div>
                    <div slot="right-icon">
                        <div class="input-unit">
                            <input type="number" v-model="mall.pay_time_limit"><span>分</span>
                        </div>
                    </div>
                </van-cell>
                <van-cell class="border-0px">
                    <div slot="title">接单时间限制</div>
                    <div slot="right-icon">
                        <div class="input-unit">
                            <input type="number" v-model="mall.handle_time_limit"><span>分</span>
                        </div>
                    </div>
                </van-cell>
                <van-cell class="border-0px">
                    <div slot="title">跑腿订单自动完成(已接单订单)</div>
                    <div slot="right-icon">
                        <div class="input-unit">
                            <input type="number" v-model="mall.auto_success_hours"><span>分</span>
                        </div>
                    </div>
                </van-cell>
            </van-cell-group>
            <van-cell-group class="margin-10-t">
                <van-cell title="订单标记"></van-cell>
                <van-cell class="border-0px">
                    <div slot="title">订单配送提前</div>
                    <div slot="right-icon">
                        <div class="input-unit">
                            <input type="number" v-model="mall.delivery_before_limit"><span>分</span>
                        </div>
                    </div>
                </van-cell>
                <van-cell class="border-0px">
                    <div slot="title">订单配送超时</div>
                    <div slot="right-icon">
                        <div class="input-unit">
                            <input type="number" v-model="mall.delivery_timeout_limit"><span>分</span>
                        </div>
                    </div>
                </van-cell>
            </van-cell-group>
            <div class="padding-15">
                <van-button size="normal" class="bg-primary font-16" block @click="onSubmit">提交</van-button>
            </div>
        </div>
        <iloading v-if="showPreLoading"></iloading>
    </div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				mall: {
					pay_time_limit: '0',
					handle_time_limit: '0',
					auto_success_hours: '0',
					delivery_before_limit: '0',
					delivery_timeout_limit: '0'
				}
			}
		},
		components: {
			publicHeader
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: this,
					autoAssign: true,
					variable: 'mall',
					url: 'plateform/errander/config/basic'
				});
			},
			onSubmit(){
				var that = this;
				setData({
					vue: this,
					url: 'plateform/errander/config/basic',
					data: that.mall
				});
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>

</style>


